import type { Controller, CangjieSelectEvent } from '@ali/4ever-cangjie';
import { SelectManager } from '../../utils/SelectManager';

export function createOnCangjieSelect(manager: SelectManager) {
  return (event: CangjieSelectEvent, controller: Controller, next) => {
    const { isSelecting } = manager;
    if (isSelecting) {
      return controller;
    }
    return next();
  };
}
